$(document).ready(function () {
  const select = () => {
    const selects = document.querySelectorAll('.js-select');
  
    selects.forEach((select) => {
      const btn = select.querySelector('.js-select-btn');
      const text = select.querySelector('.js-select-text');
      const options = select.querySelectorAll('.js-select-option');
  
      const close = () => {
        select.classList.remove('active');
      };
  
      const open = () => {
        select.classList.add('active');
      };
  
      btn.addEventListener('click', () => {
        const isActive = select.classList.contains('active');
  
        if(isActive){
          close();
        }else {
          open();
        }
      });
  
      options.forEach((option) => {
        option.addEventListener('click', () => {
          const value = option.innerText;
          text.innerText = value;
  
          close();
        });
      });
  
    	$(document).mouseup(function (e){
    		var div = $(select);
    		if (!div.is(e.target)
    		    && div.has(e.target).length === 0) {
    			div.removeClass('active');
    		}
    	});
    });
  };
  
  const rangeSlider = () => {
    $('.js-range-slider').ionRangeSlider({
      min: 0,
      max: 3,
      from: 3,
      grid: true,
      values: [
         'Не владею', 'Использую готовые <br> решения', 'Использую готовые решения <br> и умею и переделывать', 'Пишу сложный js с нуля'
       ],
    });
  };
  
  const field = () => {
    const fields = document.querySelectorAll('.js-field');
  
    fields.forEach((field) => {
      const input = field.querySelector('.js-field-input');
      const isValue = input.value.length != 0;
  
      const filled = () => {
        field.classList.add('filled');
      };
  
      const empty = () => {
        field.classList.remove('filled');
      };
  
      if(isValue){
        filled();
      }
  
      input.addEventListener('keyup', () => {
        const isValue = input.value.length != 0;
  
        if(isValue){
          filled();
        }else{
          empty();
        }
      });
    });
  };
  
  const headerMenu = function () {
  		const headers = document.querySelectorAll('.js-page-header');
  
  		headers.forEach(function (header) {
  			const toggleBtn = header.querySelector('.js-header-btn');
  			const menu = header.querySelector('.js-header-menu');
  
  			const open = function () {
  				header.classList.add('active');
  				TEST_OBJ.body.classList.add('overflow');
  			};
  
  			const close = function () {
  				header.classList.remove('active');
  				TEST_OBJ.body.classList.remove('overflow');
  			};
  
  			toggleBtn.addEventListener('click', function () {
  				const isActive = header.classList.contains('active');
          console.log(isActive);
  
  				if(isActive) {
  					close();
  				} else {
  					open();
  				}
  			});
  		});
  	};
  
  const TEST_OBJ = {};
  TEST_OBJ.body = document.querySelector('body');
  TEST_OBJ.ESC_CODE = 27;
  
  $(function() {
    select();
    rangeSlider();
    field();
    headerMenu();
  });
  
});
